ién las ecológicas, como el cambio climático y las consecuencias que puede tener el sector turístico o de aseguradoras, la influencia de las leyes de protección medioambiental o las regulaciones en materia de gestión de residuos o de energías. Y, por otro lado, las variables legales, analizando desde este factor, las leyes en defensa del consumidor, antimonopolio, licencias, legislación laboral, leyes de protección de la salud, entre otras.")])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Análisis DOFA – FODA "}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('figure',{staticClass:"px-3"},[_c('img',{staticClass:"img-240",attrs:{"src":require("@/assets/curso/temas/tema2/img-5.svg")}})])]),_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-3"},[_vm._v("Una herramienta de análisis estratégico que posibilita analizar la situación interna y externa de una empresa o proyecto; análogamente es como la fotografía de la empresa o el proyecto, en un momento determinado de tiempo. Sin embargo, al tener en cuenta que la cotidianidad no es estática y por el contrario, presenta constante evolución; además de ser una herramienta utilizada para elaborar planes de negocio, resulta conveniente repetirla en el tiempo, para reconocer la situación real en la que se desenvuelve, con el fin de planear en función de ella, la estrategia de futuro más adecuada. ")]),_c('p',[_vm._v("El nombre de esta herramienta está representado por las siglas que significan: Fortalezas, Oportunidades, Debilidades y Amenazas. Las fortalezas y debilidades forman parte del análisis interno, de las cosas que se pueden controlar, mientras que las oportunidades y las amenazas hacen parte del análisis externo, es decir, de las cosas o variables que no dependen de la empresa y, por lo tanto, no se pueden controlar. Este análisis permite realizar cruces de variables obtenidas, entendiendo que las debilidades, con la adopción de estrategias adecuadas, pueden convertirse en fortalezas; asimismo, las amenazas, con la respectiva intervención de la empresa en la medida de sus capacidades y posibilidades, pueden convertirse en oportunidades. ")])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Factores críticos de éxito (CSF) "}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('figure',{staticClass:"px-3"},[_c('img',{staticClass:"img-240",attrs:{"src":require("@/assets/curso/temas/tema2/img-6.svg")}})])]),_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-3"},[_vm._v("Son aquellos objetivos generales que la organización debe alcanzar para cumplir con los objetivos estratégicos, porque su alineación y estrecha relación con la estrategia de la empresa es directa y, por lo tanto, con alta influencia sobre los resultados estratégicos. Es decir, que si la empresa detecta estos factores críticos de éxito y logra potencializar sus resultados, materializará la generación de valor y resultados positivos a nivel estratégico para la empresa. Por ejemplo, si se define como factor crítico de éxito para el aumento de la conciencia de marca, termina siendo un objetivo ambicioso pero gran generador de valor y de presencia en el mercado. Para trabajar en el logro de este objetivo, es preciso y necesario que los equipos asignados para trabajar en el tema, puedan definir los Indicadores Clave de Rendimiento (KPI) de manera cuantitativa, para tener claridad frente al panorama sobre lo que se debe hacer, cuándo y cómo.")])])])])]),_c('p',{staticClass:"mb-5"},[_vm._v("Es así como los instrumentos de análisis representan la posibilidad de obtener información que, en sí misma, no debe ser una decisión, sino que convocan a realizar interpretaciones del contexto y de las realidades que vive la empresa o el proyecto. Ahora bien, también existen ilustraciones que posibilitan visualmente organizar la información para ser analizada de manera efectiva y eficiente; por esta razón, vamos a conocer sobre ellas.")]),_vm._m(6),_vm._m(7),_c('p',{staticClass:"mb-5"},[_vm._v("Estas combinan factores tanto internos como externos, relacionados con la industria o el sector en el que se desenvuelve la empresa y pueden ser de uno de los dos siguientes tipos: ")]),_vm._m(8),_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-md-8 col-lg-10"},[_c('div',{staticClass:"tarjeta p-3 fnd-7"},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_vm._m(9),_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_vm._m(10),_c('div',{staticClass:"col-sm-auto"},[_c('a',{staticClass:"boton color-acento-botones texto-blanco",attrs:{"href":('downloads/matrices_analisis_estrategico_anexo_cf17.pdf'),"target":"_blank"}},[_c('span',[_vm._v("Descargar ")]),_c('i',{staticClass:"fas fa-download"})])])])])])])])]),_c('separador'),_vm._m(11),_vm._m(12),_vm._m(13),_c('p',{staticClass:"mb-5"},[_vm._v("Existen diversos tipos de indicadores de gestión:")]),_vm._m(14),_c('p',{staticClass:"mb-5"},[_vm._v("Ahora, para generar o crear indicadores de gestión, se deben tener en cuenta diferentes pasos, evidenciados en la figura siguiente, porque el nivel de credibilidad y certeza del indicador, dependerá de su asertividad en la formulación:")]),_vm._m(15),_vm._m(16),_c('p',{staticClass:"mb-5"},[_vm._v("En ámbitos de la innovación, también existen indicadores útiles para medir avances en la materia; enunciemos algunos ejemplos: ")]),_vm._m(17),_vm._m(18),_vm._m(19),_c('p',{staticClass:"mb-5"},[_vm._v("Para finalizar, hablemos sobre los informes de diagnóstico, y para hacerlo, lo invitamos a ver el siguiente video.")]),_vm._m(20),_c('p',{staticClass:"mb-5"},[_vm._v("Finalizamos así la temática sobre innovación, análisis y diagnóstico empresarial.")]),_vm._m(21)],1)],1)}
